.main {
  &__inner {
    width: calc(100% - 60px);
    max-width: 1750px;
    margin: 0 auto;
  }
  &__slider {
    position: relative;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    position: relative;
    height: auto;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 50px;
    padding: 100px 0 243px;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(90.00deg, rgb(65, 64, 66) 59.183%,rgba(255, 255, 255, 0) 69.486%);
      border-radius: 50px;
      opacity: 0.3;
      pointer-events: none;
    }
  }
  &__group {
    position: relative;
    z-index: 2;
  }
  &__subtitle {
    font-size: 36px;
    line-height: 100%;
    color: #fff;
  }
  &__title {
    max-width: 965px;
    font-size: 48px;
    line-height: 100%;
    font-weight: 600;
    color: #fff;
    margin-top: 38px;
  }
  &__btn {
    height: 60px;
    display: inline-flex;
    padding: 5px 40px;
    margin-top: 38px;
    span {
      font-size: 20px;
    }
  }
  .swiper-button-prev,
  .swiper-button-next {
    &:hover {
      &:before {
        background: #fff;
      }
      svg path {
        stroke: #414042;
      }
    }
  }
  .swiper-button-prev {
    left: 70px;
  }
  .swiper-button-next {
    right: 70px;
  }
  .swiper-pagination {
    bottom: 200px;
    &-bullet {
      background: #fff;
    }
  }
}
.info {
  position: relative;
  z-index: 2;
  margin-top: -150px;
  &__inner {
    background: #fff;
    border: 4px solid #ED1C24;
    border-radius: 50px;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 120px / 3));
    gap: 60px;
  }
  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 45px;
    &:not(:last-child) {
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: -15px;
        transform: translate(0, -50%);
        width: 1px;
        height: calc(100% - 90px);
        background: rgba(65, 64, 66, 0.3);
      }
    }
    &-title {
      font-size: 40px;
      line-height: 100%;
      font-weight: 800;
    }
    &-text {
      font-size: 20px;
      line-height: 100%;
      font-weight: 500;
      margin-top: 20px;
    }
    &-icon {
      max-width: 80px;
      margin-top: 24px;
      img {
        max-width: 100%;
      }
    }
  }
}
.info2 {
  padding: 130px 0 65px;
  &__inner {
    display: grid;
    grid-template-columns: 0.95fr 1.05fr;
    align-items: stretch;
    gap: 50px;
  }
  &__desc {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: start;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 30px;
    p {
      font-size: 20px;
      line-height: 140%;
      font-weight: 500;
    }
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 30px;
    li {
      position: relative;
      padding-left: 30px;
      font-size: 20px;
      line-height: 140%;
      font-weight: 500;
      &:after {
        content: '';
        position: absolute;
        top: 10px;
        left: 0;
        width: 6px;
        height: 6px;
        background: #ED1C24;
        border-radius: 50%;
      }
    }
  }
  &__btn {
    height: 60px;
    padding: 5px 40px;
    margin-top: 30px;
    span {
      font-size: 20px;
    }
  }
  &__img {
    img {
      width: 100%;
      border-radius: 50px;
    }
  }
}
.products {
  padding: 65px 0;
  &__title {
    text-align: center;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(5, calc(100% / 5 - 120px / 5));
    gap: 40px 30px;
    margin-top: 60px;
  }
}
.product-card {
  &:hover {
    .product-card__img {
      border-color: #ED1C24;
    }
  }
  &__img {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F4F4F4;
    border: 2px solid transparent;
    border-radius: 10px;
    padding: 10px 10px 94.535% 10px;
    img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      max-width: 80%;
      max-height: 80%;
    }
  }
  &__name {
    display: inline-block;
    font-size: 18px;
    line-height: 110%;
    font-weight: 500;
    margin-top: 10px;
  }
}
