      margin-top: 40px;
    }
    &__btn {
      width: 100%;
    }
  }
  .news-card {
    max-width: 100%;
  }
  .about {
    .info2__title {
      margin-bottom: 30px;
    }
  }
  .advantages {
    &__items {
      gap: 10px;
      margin-top: 30px;
    }
    &__item {
      padding: 20px;
      &-icon {
        height: auto;
      }
      &-title {
        min-height: unset;
        font-size: 16px;
      }
    }
  }
  .partners {
    padding-bottom: 80px;
    &__slider {
      margin-top: 30px;
    }
  }
}

@media screen and (max-width: 374px) {
  .info {
    &__item {
      padding-left: 50px;
      &-title {
        font-size: 20px;
      }
      &-icon {
        width: 40px;
      }
    }
  }
  .application {
    &__title {
      font-size: 18px;
    }
  }
  .news {
    &__slider-nav {
      gap: 40px;
    }
  }
  .advantages {
    &__title {
      font-size: 24px;
    }
  }
  .partners {
    &__slider-nav {
      gap: 40px;
    }
  }
}

@media screen and (max-width: 360px) {
  .application {
    &__wrap {
      padding-left: 10px;
      padding-right: 10px;
