.catalog {
  padding: 30px 0 160px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 50px;
  }
  &__content {
    width: calc(100% - 350px);
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
    gap: 40px 20px;
    margin-top: 60px;
  }
  .product-card {
    &__img {
      padding-bottom: 115%;
    }
  }
  .pagination {
    margin-top: 130px;
  }
  .filter-btn {
    width: 100%;
    height: 50px;
    display: none;
    gap: 8px;
    &__text {
      font-size: 18px;
    }
    &__icon {
      width: 17px;
      display: flex;
      align-items: center;
      svg {
        width: 100%;
        transition: all 0.3s;
      }
    }
  }
  .item-show--active {
    .filter-btn__icon {
      svg {
        transform: rotate(-180deg);
      }
    }
  }
  &-form {
    &__wrap {
      margin-top: 50px;
    }
    &__title {
      font-size: 24px;
      line-height: 100%;
      text-transform: uppercase;
    }
    &__group {
      margin-top: 40px;
    }
    &__subtitle {
      font-size: 16px;
      margin-bottom: 12px;
    }
    &__checkboxes-wrap {
      border: 1px solid #E0DFE1;
      border-radius: 10px;
      padding: 20px;
    }
    &__checkboxes {
      max-height: 376px;
      display: grid;
      grid-template-columns: 100%;
      gap: 10px;
      overflow-y: auto;
      &::-webkit-scrollbar {
        width: 5px;
      }
      &::-webkit-scrollbar-track {
        background: #fff;
        border-radius: 5px;
      }
      &::-webkit-scrollbar-thumb {
        background: #D9D9D9;
        border-radius: 5px;
      }
    }
    &__checkbox {
      position: relative;
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        padding: 0;
        margin: 0;
        opacity: 0;
      }
      input:checked ~ label {
        &:before {
          opacity: 1;
        }
      }
      label {
        position: relative;
        display: inline-block;
        padding-left: 40px;
        font-size: 20px;
        line-height: 140%;
        font-weight: 500;
        &:after {
          content: '';
          position: absolute;
          top: 4px;
          left: 0;
          width: 20px;
          height: 20px;
          border: 1px solid #414042;
          border-radius: 5px;
        }
        &:before {
          content: '';
          position: absolute;
          top: 10px;
          left: 6px;
          width: 8px;
          height: 8px;
          background: #ED1C24;
          border-radius: 2px;
          opacity: 0;
        }
      }
    }
    hr {
      height: 20px;
      padding: 0;
      margin: 0;
      opacity: 0;
      pointer-events: none;
    }
    &__price-wrap {
      display: grid;
      grid-template-columns: repeat(2, calc(50% - 40px / 2));
      gap: 40px;
    }
    &__price {
      position: relative;
      &:not(:last-child) {
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          right: -26px;
          transform: translate(0, -50%);
          width: 13px;
          height: 1px;
          background: #414042;
        }
      }
      input {
        width: 100%;
        height: 50px;
        background: transparent;
        border: 1px solid #939293;
        border-radius: 10px;
        padding: 0 14px 0 40px;
        font-size: 20px;
        line-height: 140%;
        font-weight: 500;
      }
      span {
        position: absolute;
        z-index: 2;
        top: 50%;
        left: 14px;
        transform: translate(0, -50%);
        font-size: 16px;
        line-height: 140%;
        color: #939293;
      }
    }
    .nice-select {
      width: 100%;
      min-width: unset;
      height: 50px;
      display: flex;
      align-items: center;
      background: transparent;
      border: 1px solid #939293;
      border-radius: 10px;
      padding: 0 14px;
      font-size: 20px;
      line-height: 140%;
      font-weight: 500;
      &:after {
        content: '';
        top: 50%;
        right: 14px;
        transform: translate(0, -50%);
        width: 12px;
        height: 6px;
        background-image: url("../img/arrow-bottom.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
      }
      .list {
        width: calc(100% + 1.5px);
        background: #fff;
        border: 1px solid #939293;
        border-top: unset;
        box-shadow: unset;
        border-radius: 0 0 10px 10px;
        padding: 12px 14px 14px 14px;
        color: #414042;
        margin-top: 0;
        margin-left: -1px;
        li {
          border: none;
          border-radius: 10px;
          padding: 10px;
          font-size: 20px;
          line-height: 140%;
          font-weight: 500;
          &:hover {
            background: #F4F4F4;
          }
        }
        .selected {
          background: #F4F4F4;
        }
      }
    }
    .open {
      border-bottom: unset;
      border-radius: 10px 10px 0 0;
      color: #ED1C24;
      &:after {
        transform: translate(0, -50%) rotate(-180deg);
      }
    }
    &__btns {
      display: grid;
      grid-template-columns: 100%;
      gap: 12px;
      margin-top: 40px;
    }
    &__btn {
      width: 100%;
      height: 60px;
      padding: 5px 15px;
      span {
        font-size: 20px;
      }
    }
  }
}
.catalog-sidebar {
  width: 300px;
  .catalog__title {
    display: none;
  }
  &__title {
    display: flex;
    align-items: start;
    gap: 10px;
    i {
      width: 24px;
      display: flex;
      align-items: center;
      img {
        width: 100%;
        margin-top: -3px;
      }
    }
    span {
      font-size: 20px;
      line-height: 100%;
      font-weight: 700;
      text-decoration: underline;
    }
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    margin-top: 20px;
    p {
      font-size: 16px;
      line-height: 140%;
      font-weight: 500;
    }
  }
}

@media screen and (max-width: 1600px) {
  .catalog {
    padding: 24px 0 130px;
    &__inner {
      gap: 25px;
    }
    &__content {
      width: calc(100% - 290px);
    }
    &__cards {
      margin-top: 40px;
    }
    .pagination {
      margin-top: 80px;
    }
    &-form {
      &__wrap {
        margin-top: 40px;
      }
      &__subtitle {
        font-size: 14px;
      }
      &__checkboxes {
        max-height: 310px;
      }
      &__checkbox {
        label {
          font-size: 18px;
          &:before {
            top: 9px;
          }
          &:after {
            top: 3px;
          }
        }
      }
      &__price {
        input {
          font-size: 18px;
        }
      }
      .nice-select {
        font-size: 18px;
        .list {
          li {
            font-size: 18px;
          }
        }
      }
      &__btn {
        height: 50px;
        span {
          font-size: 18px;
        }
      }
    }
  }
  .catalog-sidebar {
    width: 265px;
    &__title {
      i {
        width: 18px;
        img {
          margin-top: 0;
        }
      }
      span {
        font-size: 18px;
      }
    }
    &__text {
      p {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .catalog {
    &__inner {
      flex-direction: column;
    }
    &__content {
      width: 100%;
      .catalog__title {
        display: none;
      }
    }
    &__title {
      text-align: center;
    }
    &__cards {
      margin-top: 0;
    }
    .pagination {
      justify-content: center;
    }
    .filter-btn {
      display: flex;
      margin-top: 20px;
    }
    .filter-content {
      display: none;
      margin-top: 20px;
    }
  }
  .catalog-sidebar {
    width: 100%;
    .catalog__title {
      display: block;
    }
  }
}

@media screen and (max-width: 576px) {
  .catalog {
    padding: 20px 0 80px;
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 15px / 2));
      gap: 30px 15px;
    }
    .pagination {
      margin-top: 40px;
    }
  }
}