.wrapper {
  overflow: hidden;
}
.h2 {
  font-size: 40px;
  line-height: 100%;
  font-weight: 600;
  text-transform: uppercase;
  span {
    color: #ED1C24;
  }
}
.btn-red {
  background: #ED1C24;
  border-radius: 20px;
  color: #fff;
  &:hover {
    background: #FA666C;
  }
  span {
    line-height: 100%;
    font-weight: 700;
  }
}
.btn-gray {
  background: #E0DFE1;
  border-radius: 20px;
  &:hover {
    background: #F4F4F4;
  }
  span {
    line-height: 100%;
    font-weight: 700;
  }
}
.btn-black {
  background: #414042;
  border-radius: 20px;
  color: #fff;
  &:hover {
    background: #747474;
  }
  span {
    line-height: 100%;
    font-weight: 700;
  }
}
.btn-black__outline {
  background: transparent;
  border: 1px solid #414042;
  border-radius: 20px;
  &:hover {
    border-color: #ED1C24;
  }
  span {
    line-height: 100%;
    font-weight: 700;
  }
}
.swiper-button-prev,
.swiper-button-next {
  z-index: 1;
  width: 24px;
  &:before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100px;
    height: 100px;
    background: transparent;
    border-radius: 20px;
  }
  &:after {
    display: none;
  }
  svg {
    position: relative;
    z-index: 2;
    width: 100%;
    height: auto;
  }
}
.swiper-pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  &-bullet {
    width: 6px;
    height: 6px;
    margin: 0 20px !important;
    opacity: 1 !important;
    &-active {
      width: 12px;
      height: 12px;
    }
  }
}
.tg {
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #34AADF;
  border-radius: 50%;
  &:hover {
    background: #7DD7FF;
  }
  img {
    max-width: 80%;
    max-height: 80%;
  }
}
.vk {
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #2787F5;
  border-radius: 50%;
  &:hover {
    background: #68AEFF;
  }
  img {
    max-width: 80%;
    max-height: 80%;
  }
}
.up {
  position: fixed;
  z-index: 5;
  right: 30px;
  bottom: 100px;
  transform: scale(0);
  width: 100px;
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: #fff;
  border: 2px solid #ED1C24;
  border-radius: 20px;
  padding: 5px;
  pointer-events: none;
  transition: all 0.125s;
  &--active {
    transform: scale(1);
    pointer-events: auto;
  }
  &:hover {
    border-width: 6px;
  }
  &-icon {
    width: 44px;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &-text {
    font-size: 16px;
    line-height: 140%;
    font-weight: 500;
    text-transform: uppercase;
  }
}
.modal {
  position: fixed;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: rgba(65, 64, 66, 0.5);
  padding: 30px 15px;
  opacity: 0;
  pointer-events: none;
  overflow-y: auto;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    position: relative;
    width: calc(100% - 30px);
    background: #fff;
    border-radius: 50px;
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      margin: 50px auto;
    }
  }
  &__close {
    position: absolute;
    z-index: 2;
    top: 25px;
    right: 25px;
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    border: 1px solid #E0DFE1;
    border-radius: 50%;
    padding: 5px;
    img {
      width: 24px;
    }
  }
  &__logo {
    width: 250px;
    display: block;
    img {
      width: 100%;
    }
  }
}
.modal-form {
  .modal__dialog {
    max-width: 1200px;
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(2, calc(50%));
  }
  &__desc {
    border-radius: 50px 0 0 50px;
    padding: 30px 50px 50px;
  }
  &__img {
    img {
      width: 100%;
      height: 100%;
      border-radius: 0 50px 50px 0;
    }
  }
  .form {
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    margin-top: 40px;
    &-check {
      margin-top: 5px;
    }
    &-btn {
      margin-top: 25px;
    }
  }
  &__title {
    text-align: center;
    margin-top: 30px;
  }
  &__subtitle {
    text-align: center;
    font-size: 24px;
    line-height: 100%;
    font-weight: 600;
    color: #ED1C24;
    margin-top: 5px;
  }
  &__text {
    text-align: center;
    font-size: 24px;
    line-height: 100%;
    margin-top: 40px;
  }
  &__info {
    max-width: 285px;
    text-align: center;
    font-size: 16px;
    line-height: 100%;
    color: #939293;
    margin: 10px auto 0;
  }
  &__group {
    margin-top: 30px;
  }
  &__subtitle2 {
    text-align: center;
    font-size: 16px;
    line-height: 100%;
  }
  &__phones {
    max-width: 390px;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 10px / 2));
    gap: 5px 10px;
    margin: 10px auto 0;
  }
  &__phone {
    font-size: 18px;
    line-height: 100%;
    font-weight: 500;
    &:hover {
      color: #ed1c24;
    }
  }
  &__btn {
    width: 100%;
    height: 60px;
    padding: 5px 15px;
    margin-top: 60px;
    span {
      font-size: 20px;
    }
  }
}
.modal-form2 {
  .modal__dialog {
    max-width: 600px;
  }
  .modal-form__content {
    grid-template-columns: 100%;
  }
  .modal-form__desc {
    border-radius: 50px;
  }
}
.cookie {
  padding-bottom: 100px;
  @media screen and (min-width: 993px) {
    display: flex;
    text-align: start;
  }
  .modal__dialog {
    max-width: 454px;
    margin: auto auto 0 0;
  }
  .modal-form__content {
    grid-template-columns: 100%;
  }
  .modal-form__desc {
    border-radius: 50px;
  }
  &-text {
    text-align: center;
    font-size: 16px;
    line-height: 100%;
    a {
      text-decoration: underline;
      &:hover {
        color: #ed1c24;
        text-decoration: unset;
      }
    }
  }
  .modal-form__btn {
    margin-top: 30px;
  }
}
.form {
  &-group {
    position: relative;
    input {
      width: 100%;
      height: 50px;
      background: #F4F4F4;
      border: 1px solid transparent;
      border-radius: 20px;
      padding-right: 20px;
      font-size: 16px;
      line-height: 140%;
      font-weight: 700;
      &:focus {
        border-color: #414042;
      }
    }
    textarea {
      width: 100%;
      height: 100px;
      background: #F4F4F4;
      border: 1px solid transparent;
      border-radius: 20px;
      padding: 12px 20px;
      resize: none;
      font-size: 16px;
      line-height: 140%;
      font-weight: 700;
      &::placeholder {
        font-weight: 400;
      }
      &:focus {
        border-color: #414042;
      }
    }
    .input-title {
      position: absolute;
      z-index: 2;
      top: 50%;
      left: 20px;
      transform: translate(0, -50%);
      font-size: 16px;
      line-height: 140%;
    }
  }
  .input-name {
    input {
      padding-left: 67px;
    }
  }
  .input-company {
    input {
      padding-left: 186px;
    }
  }
  .input-inn {
    input {
      padding-left: 202px;
    }
  }
  .input-city {
    input {
      padding-left: 76px;
    }
  }
  .input-email {
    input {
      padding-left: 70px;
    }
  }
  .input-phone {
    input {
      padding-left: 96px;
    }
  }
  .error {
    input {
      border-color: #ED1C24;
      background: #FEE9EA;
    }
  }
  &-check {
    position: relative;
    input {
      position: absolute;
      z-index: 3;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      opacity: 0;
    }
    input:checked ~ label {
      &:before {
        background-image: url("../img/check.svg");
      }
    }
    label {
      position: relative;
      display: block;
      padding-left: 36px;
      font-size: 16px;
      line-height: 140%;
      &:before {
        content: '';
        position: absolute;
        z-index: 2;
        top: -7px;
        left: 5px;
        width: 27px;
        height: 23px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
      }
      &:after {
        content: '';
        position: absolute;
        top: -1px;
        left: 0;
        width: 22px;
        height: 22px;
        border: 1px solid #959697;
        border-radius: 3px;
      }
      a {
        position: relative;
        z-index: 3;
        &:hover {
          color: #ED1C24;
          text-decoration: underline;
        }
      }
    }
  }
  &-btn {
    height: 60px;
    padding: 5px 15px;
    span {
      font-size: 20px;
    }
  }
}
.breadcrumbs {
  padding: 35px 0 30px 0;
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 10px 20px;
  }
  &__item {
    position: relative;
    font-size: 16px;
    line-height: 100%;
    &:not(:last-child) {
      padding-right: 20px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: 0;
        transform: translate(0, -50%);
        width: 1px;
        height: 20px;
        background: #C0C0C0;
      }
    }
    &--active {
      color: #9A999A;
    }
    &:hover {
      color: #9A999A;
    }
  }
}
.pagination {
  display: flex;
  align-items: center;
  gap: 60px;
  &-prev,
  &-next {
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F4F4F4;
    border-radius: 10px;
    &:hover {
      background: #E0DFE1;
    }
    img {
      width: 24px;
    }
  }
  &-items {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  &-item {
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F4F4F4;
    border-radius: 10px;
    font-size: 24px;
    line-height: 100%;
    font-weight: 600;
    &--active {
      background: #414042;
      color: #fff;
    }
    &:hover {
      background: #414042;
      color: #fff;
    }
  }
}

@media screen and (max-width: 1750px) {
  .swiper-button-prev,
  .swiper-button-next {
    width: 15px;
    &:before {
      width: 60px;
      height: 60px;
    }
  }
}

@media screen and (max-width: 1600px) {
  .h2 {
    font-size: 36px;
  }
  .modal {
    &__logo {
      width: 200px;
    }
  }
  .modal-form {
    .modal__dialog {
      max-width: 1000px;
    }
    &__title {
      margin-top: 20px;
    }
    &__subtitle {
      font-size: 18px;
    }
    .form {
      margin-top: 30px;
    }
    &__text {
      font-size: 20px;
      margin-top: 30px;
    }
    &__info {
      max-width: 285px;
      font-size: 14px;
      margin-top: 5px;
    }
    &__group {
      margin-top: 20px;
    }
    &__subtitle2 {
      font-size: 14px;
    }
    &__phones {
      max-width: 350px;
    }
    &__phone {
      font-size: 16px;
    }
    &__btn {
      height: 50px;
      margin-top: 40px;
      span {
        font-size: 18px;
      }
    }
  }
  .modal-form2 {
    .modal__dialog {
      max-width: 480px;
    }
  }
  .cookie {
    .modal__dialog {
      max-width: 420px;
    }
    &-text {
      font-size: 14px;
    }
  }
  .form {
    &-group {
      input {
        height: 40px;
        border-radius: 10px;
        font-size: 14px;
      }
      textarea {
        height: 94px;
        border-radius: 10px;
        font-size: 14px;
      }
    }
    .input-name {
      input {
        padding-left: 62px;
      }
    }
    .input-company {
      input {
        padding-left: 181px;
      }
    }
    .input-inn {
      input {
        padding-left: 197px;
      }
    }
    .input-city {
      input {
        padding-left: 71px;
      }
    }
    .input-email {
      input {
        padding-left: 65px;
      }
    }
    .input-phone {
      input {
        padding-left: 91px;
      }
    }
    &-check {
      label {
        font-size: 14px;
      }
    }
    &-btn {
      height: 50px;
      span {
        font-size: 18px;
      }
    }
  }
  .breadcrumbs {
    padding: 24px 0;
    &__item {
      font-size: 14px;
    }
  }
  .pagination {
    gap: 50px;
    &-prev,
    &-next {
      width: 50px;
      height: 50px;
    }
    &-item {
      width: 50px;
      height: 50px;
      font-size: 20px;
    }
  }
}

@media screen and (max-width: 992px) {
  .up {
    display: none;
  }
  .modal {
    padding: 0 15px;
  }
  .modal {
    &__dialog {
      border-radius: 30px;
    }
  }
  .modal-form {
    &__desc {
      border-radius: 30px 0 0 30px;
      padding: 30px;
    }
    &__img {
      img {
        border-radius: 0 30px 30px 0;
      }
    }
  }
  .modal-form2 {
    .modal-form__desc {
      border-radius: 30px;
    }
  }
  .cookie {
    display: flex;
    justify-content: center;
    padding: 30px 15px 60px;
    .modal__dialog {
      margin: auto 0 0 0;
    }
    .modal-form__desc {
      border-radius: 30px;
    }
  }
}

@media screen and (max-width: 768px) {
  .modal-form {
    &__content {
      grid-template-columns: 100%;
    }
    &__img {
      display: none;
    }
  }
  .pagination {
    justify-content: space-between;
    gap: 10px;
  }
}

@media screen and (max-width: 600px) {
  .pagination {
    justify-content: space-between !important;
    .remove--mob {
      display: none;
    }
  }
}

@media screen and (max-width: 576px) {
  .h2 {
    font-size: 26px;
  }
  .btn-red,
  .btn-gray,
  .btn-black,
  .btn-black__outline {
    border-radius: 16px;
  }
  .swiper-pagination {
    &-bullet {
      width: 4px;
      height: 4px;
      margin: 0 15px !important;
      &-active {
        width: 8px;
        height: 8px;
      }
    }
  }
  .modal {
    &__dialog {
      width: 100%;
    }
    &__logo {
      width: 140px;
    }
    &__close {
      top: 15px;
      right: 15px;
      width: 40px;
      height: 40px;
    }
  }
  .modal-form {
    &__desc {
      padding: 20px 15px 40px;
    }
    &__phones {
      grid-template-columns: 100%;
      gap: 5px;
      text-align: center;
    }
  }
  .breadcrumbs {
    padding: 20px 0;
    &__item {
      padding-right: 0 !important;
      &:after {
        display: none !important;
      }
    }
  }
  .pagination {
    gap: 5px;
    &-prev,
    &-next {
      width: 40px;
      height: 40px;
    }
    &-items {
      gap: 5px;
    }
    &-item {
      width: 40px;
      height: 40px;
    }
  }
}