.product-main {
  padding: 30px 0 65px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 50px;
  }
  .gallery {
    width: 53.6%;
  }
  .gallery__big {
    .swiper-slide {
      position: relative;
      display: block;
      background: #F4F4F4;
      border-radius: 50px;
      padding-bottom: 67%;
      img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        max-width: 80%;
        max-height: 80%;
      }
    }
  }
  .gallery__small {
    max-width: 430px;
    margin: 0;
    &-wrap {
      position: relative;
      display: flex;
      justify-content: space-between;
      align-items: start;
      gap: 30px;
      padding-right: 40px;
      margin-top: 20px;
      .threed {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        cursor: pointer;
        &-img {
          width: 150px;
          height: 150px;
          display: flex;
          justify-content: center;
          align-items: center;
          background: #F4F4F4;
          border-radius: 10px;
          img {
            max-width: 80%;
            max-height: 80%;
          }
        }
        &-text {
          font-size: 16px;
          line-height: 140%;
          font-weight: 500;
          margin-top: 5px;
        }
      }
    }
    .swiper-slide {
      position: relative;
      background: #F4F4F4;
      border: 1px solid transparent;
      border-radius: 10px;
      padding-bottom: 23.26%;
      cursor: pointer;
      img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        max-width: 80%;
        max-height: 80%;
      }
    }
    .swiper-slide-thumb-active {
      border-color: #ED1C24;
    }
  }
  &__desc {
    width: 46.4%;
  }
  &__subtitle {
    font-size: 24px;
    line-height: 100%;
    margin-top: 10px;
  }
  &__price {
    font-size: 32px;
    line-height: 100%;
    font-weight: 600;
    margin-top: 40px;
  }
  &__btn {
    height: 60px;
    padding: 5px 40px;
    margin-top: 20px;
    span {
      font-size: 20px;
    }
  }
  &__download {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-top: 40px;
    i {
      width: 24px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 20px;
      line-height: 100%;
      font-weight: 700;
    }
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 40px;
    p {
      font-size: 16px;
      line-height: 140%;
      font-weight: 500;
    }
  }
}
.product-info {
  padding: 65px 0 130px;
  &__title {
    font-size: 32px;
    line-height: 100%;
    font-weight: 600;
  }
}
.ids {
  margin-top: 50px;
  &-head {
    background: #414042;
    border-radius: 10px;
    padding: 32px;
  }
  &-items {
    display: flex;
    justify-content: center;
    align-items: stretch;
    gap: 30px;
  }
  &-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    &__number {
      flex-grow: 1;
      display: flex;
      align-items: flex-end;
      border-bottom: 2px solid #fff;
      padding-bottom: 12px;
      font-size: 32px;
      line-height: 100%;
      font-weight: 600;
      color: #ED1C24;
    }
    &__number2 {
      padding-top: 12px;
      font-size: 20px;
      line-height: 100%;
      font-weight: 500;
      color: #fff;
    }
  }
  &-body {
    margin-top: 10px;
  }
  &-items2 {
    display: grid;
    grid-template-columns: 100%;
    gap: 5px;
  }
  &-item2 {
    display: flex;
    align-items: stretch;
    gap: 5px;
    &__number {
      width: 60px;
      min-height: 60px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #414042;
      border-radius: 5px;
      font-size: 20px;
      line-height: 100%;
      font-weight: 500;
      color: #fff;
    }
    &__text {
      min-height: 60px;
      flex-grow: 1;
      display: flex;
      align-items: center;
      background: #F4F4F4;
      border-radius: 5px;
      padding: 18px 24px;
      font-size: 20px;
      line-height: 100%;
      font-weight: 500;
    }
  }
  &-text {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 40px;
    p {
      font-size: 20px;
      line-height: 140%;
      font-weight: 500;
    }
  }
}
.product-table,
.product-table2,
.product-table3,
.product-table4 {
  margin-top: 130px;
  caption {
    background: #ED1C24;
    border-radius: 10px;
    padding: 24px 20px;
    font-size: 32px;
    line-height: 100%;
    font-weight: 600;
    color: #fff;
    margin-bottom: 5px;
  }
  table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 5px;
    thead {
      tr th {
        position: relative;
        background: #939293;
        border-radius: 10px;
        line-height: 100%;
        font-weight: 600;
        color: #fff;
      }
    }
    tbody {
      tr td {
        position: relative;
        text-align: center;
        background: #F4F4F4;
        border-radius: 5px;
        padding: 16px 5px;
        font-size: 20px;
        line-height: 140%;
        font-weight: 500;
      }
    }
  }
  .table-text {
    font-size: 20px;
    line-height: 140%;
    font-weight: 500;
    margin-top: 40px;
  }
}
.product-table5 {
  margin-top: 130px;
  table {
    border-collapse: collapse;
    thead {
      tr th {
        min-width: 98px;
        background: #939293;
        border: 2px solid #fff;
        border-radius: 5px;
        padding: 30px 10px;
        font-size: 18px;
        line-height: 100%;
        font-weight: 600;
        color: #fff;
      }
      .th-big {
        font-size: 24px;
      }
      .th-vertical {
        writing-mode: sideways-lr;
        text-orientation: mixed;
      }
      .tr-numbers {
        th {
          min-width: unset;
          background: #F4F4F4;
          padding: 10px 5px;
          color: #414042;
        }
      }
    }
    tbody {
      tr td {
        text-align: center;
        border-top: 2px dashed #414042;
        border-left: 2px solid rgba(65, 64, 66, 0.2);
        border-right: 2px solid rgba(65, 64, 66, 0.2);
        padding: 10px 5px;
        font-size: 24px;
        line-height: 100%;
      }
      tr td:first-child {
        border-left: unset;
      }
      tr td:last-child {
        border-right: unset;
      }
      tr:last-child,
      tr:nth-last-child(2) {
        td {
          border-bottom: 2px dashed #414042;
        }
      }
      .table-img,
      .table-icon {
        img {
          max-width: 90%;
          max-height: 90%;
        }
      }
    }
  }
}
.product-table6 {
  margin-top: 130px;
  .table-title {
    text-align: center;
    background: #ED1C24;
    border-radius: 10px;
    padding: 24px 20px;
    font-size: 32px;
    line-height: 100%;
    font-weight: 600;
    color: #fff;
    margin-bottom: 5px;
  }
  &__flex {
    display: flex;
    align-items: start;
    gap: 95px;
  }
  &__images {
    width: 280px;
    flex-shrink: 0;
  }
  &__img {
    img {
      max-width: 100%;
    }
  }
  table {
    width: calc(100% - 375px);
    border-collapse: collapse;
    thead {
      tr th {
        background: #939293;
        padding: 12px 10px;
        font-size: 18px;
        line-height: 100%;
        font-weight: 600;
        color: #fff;
      }
      tr:nth-child(1) {
        th:nth-child(1) {
          border-radius: 5px 0 0 5px;
        }
        th:nth-child(2) {
          border-radius: 0 5px 0 0;
        }
      }
      tr:nth-child(2) {
        th:last-child {
          border-radius: 0 0 5px 0;
        }
      }
    }
    tbody {
      tr td {
        text-align: center;
        border-top: 2px dashed #414042;
        padding: 10px 5px;
        font-size: 18px;
        line-height: 100%;
      }
    }
  }
}
//////
.product-table {
  table {
    tbody {
      tr td {
        min-width: 199px;
      }
      tr td:nth-child(1) {
        text-align: start;
        padding-left: 25px;
        padding-right: 25px;
      }
      .colspan-3 {
        display: grid;
        grid-template-columns: repeat(3, calc(100% / 3 - 10px / 3));
        gap: 5px;
      }
    }
  }
}
.product-table2 {
  caption {
    margin-bottom: 0;
  }
  table {
    thead {
      tr th {
        padding: 14px 15px;
        font-size: 32px;
      }
    }
    tbody {
      tr td:nth-child(1) {
        text-align: start;
        padding-left: 25px;
        padding-right: 25px;
      }
      tr td:nth-child(2) {
        width: 300px;
      }
    }
  }
}
.product-table3,
.product-table4 {
  table {
    table-layout: fixed;
    thead {
      tr th {
        padding: 22px 10px;
        font-size: 18px;
      }
      .margin,
      .margin2 {
        &:after {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: calc(100% - 7.5px);
          height: 100%;
          background: #939293;
          border-radius: 10px;
        }
        span {
          padding: 22px 10px;
        }
      }
      .margin2 {
        &:after {
          left: unset;
          right: 0;
        }
      }
    }
    tbody {
      .margin,
      .margin2 {
        &:after {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: calc(100% - 7.5px);
          height: 100%;
          background: #F4F4F4;
          border-radius: 10px;
        }
        span {
          padding: 16px 5px;
        }
      }
      .margin2 {
        &:after {
          left: unset;
          right: 0;
        }
      }
    }
  }
  .margin {
    background: transparent;
    padding: 0;
    span {
      position: relative;
      z-index: 2;
      width: calc(100% - 7.5px);
      height: 100%;
      display: block;
      margin-right: 7.5px;
    }
  }
  .margin2 {
    background: transparent;
    padding: 0;
    span {
      position: relative;
      z-index: 2;
      width: calc(100% - 7.5px);
      height: 100%;
      display: block;
      margin-left: 7.5px;
    }
  }
}


