.contacts {
  padding: 30px 0 160px;
  &__group {
    display: flex;
    align-items: center;
    gap: 30px;
    margin-top: 60px;
  }
  &__text {
    max-width: 680px;
    font-size: 24px;
    line-height: 140%;
    font-weight: 500;
  }
  &__download {
    height: 60px;
    flex-shrink: 0;
    gap: 10px;
    padding: 5px 40px;
    i {
      width: 25px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 20px;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 60px / 2));
    gap: 10px 60px;
    margin-top: 60px;
  }
  &__item {
    &-title {
      display: flex;
      align-items: center;
      gap: 10px;
      i {
        width: 25px;
        flex-shrink: 0;
        display: flex;
        align-items: center;
        img {
          width: 100%;
        }
      }
      span {
        font-size: 32px;
        line-height: 100%;
        font-weight: 600;
      }
    }
    &-text {
      margin-top: 20px;
      p {
        font-size: 20px;
        line-height: 140%;
        font-weight: 500;
      }
    }
    &-subgroup {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      gap: 10px 30px;
      margin-top: 40px;
    }
  }
  &__phone,
  &__email {
    display: flex;
    align-items: center;
    gap: 10px;
    &:hover {
      span {
        color: #ED1C24;
      }
    }
    i {
      width: 24px;
      display: flex;
      align-items: center;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 20px;
      line-height: 100%;
      font-weight: 500;
      transition: all 0.3s;
    }
  }
  &__email {
    &:hover {
      span {
        text-decoration: unset;
      }
    }
    span {
      text-decoration: underline;
    }
  }
  &__items2 {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
    gap: 20px;
    margin-top: 100px;
  }
  &__item2 {
    background: #F4F4F4;
    border-radius: 20px;
    padding: 30px 40px;
    &-title {
      font-size: 20px;
      line-height: 140%;
      font-weight: 700;
    }
    &-subgroup {
      display: inline-flex;
      flex-direction: column;
      align-items: start;
      gap: 20px;
      margin-top: 30px;
    }
  }
  &__phones {
    display: inline-flex;
    flex-direction: column;
    align-items: start;
    gap: 10px;
  }
  .map {
    width: 100%;
    height: 500px;
    margin-top: 60px;
    iframe {
      width: 100%;
      height: 100%;
      border: none;
      border-radius: 50px;
    }
  }
}

@media screen and (max-width: 1600px) {
  .contacts {
    padding: 24px 0 130px;
    &__group {
      margin-top: 40px;
    }
    &__text {
      max-width: 510px;
      font-size: 18px;
    }
    &__download {
      height: 50px;
      span {
        font-size: 18px;
      }
    }
    &__item {
      &-title {
        span {
          font-size: 26px;
        }
      }
      &-text {
        max-width: 500px;
        p {
          font-size: 16px;
        }
      }
      &-subgroup {
        margin-top: 30px;
      }
    }
    &__phone,
    &__email {
      span {
        font-size: 16px;
      }
    }
    &__items2 {
      margin-top: 60px;
    }
    &__item2 {
      &-title {
        font-size: 18px;
      }
      &-subgroup {
        margin-top: 20px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .contacts {
    &__items2 {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
    }
  }
}

@media screen and (max-width: 768px) {
  .contacts {
    &__group {
      flex-direction: column;
      align-items: start;
    }
    &__items {
      grid-template-columns: 100%;
      gap: 30px;
    }
    &__items2 {
      grid-template-columns: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  .contacts {
    padding: 20px 0 80px;
    &__group {
      margin-top: 30px;
    }
    &__download {
      width: 100%;
      padding: 5px 15px;
    }
    &__item {
      &-title {
        span {
          font-size: 24px;
        }
      }
    }
    &__items2 {
      gap: 10px;
    }
    &__item2 {
      padding: 30px 24px;
    }
  }
}