.general {
  padding: 30px 0 160px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 50px;
  }
  .sidebar {
    width: 300px;
    flex-shrink: 0;
    &__list {
      display: grid;
      grid-template-columns: 100%;
      gap: 12px;
    }
    &__link {
      font-size: 20px;
      line-height: 140%;
      font-weight: 500;
      &--active {
        color: #ED1C24;
        text-decoration: underline;
      }
      &:hover {
        color: #ED1C24;
      }
    }
  }
  &__content {
    width: calc(100% - 350px);
  }
  &__title {
    margin-bottom: 60px;
  }
  .pagination {
    margin-top: 90px;
  }
}
.vacancies {
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 30px;
    background: #414042;
    border-radius: 20px;
    padding: 60px;
    color: #fff;
  }
  &__group {
    display: grid;
    grid-template-columns: 100%;
    gap: 40px;
    &:nth-child(2) {
      flex-shrink: 0;
    }
  }
  &__subtitle {
    font-size: 24px;
    line-height: 100%;
    text-transform: uppercase;
  }
  &__subgroup {
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 10px;
  }
  &__name {
    font-size: 20px;
    line-height: 140%;
    font-weight: 700;
  }
  &__post {
    font-size: 16px;
    line-height: 100%;
    font-weight: 500;
  }
  &__download {
    display: flex;
    align-items: center;
    gap: 10px;
    i {
      width: 24px;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 20px;
      line-height: 100%;
      font-weight: 700;
    }
  }
  &__btn {
    height: 60px;
    padding: 5px 40px;
    span {
      font-size: 20px;
    }
  }
  &__phone,
  &__email {
    display: flex;
    align-items: center;
    gap: 10px;
    &:hover {
      span {
        color: #ED1C24;
      }
    }
    i {
      width: 24px;
      display: flex;
      align-items: center;
      img {
        width: 100%;
      }
    }
    span {
      display: inline-block;
      font-size: 20px;
      line-height: 100%;
      font-weight: 500;
      transition: all 0.3s;
    }
  }
  &__email {
    &:hover {
      span {
        text-decoration: unset;
      }
    }
    span {
      text-decoration: underline;
    }
  }
  &__nav {
    margin-top: 100px;
  }
  &__subtitle2 {
    font-size: 32px;
    line-height: 100%;
    font-weight: 600;
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 5px;
    margin-top: 60px;
    li a {
      width: 100%;
      display: block;
      background: #F4F4F4;
      border-radius: 10px;
      padding: 20px;
      font-size: 20px;
      line-height: 140%;
      font-weight: 500;
      &:hover {
        background: #414042;
        color: #fff;
      }
    }
  }
  &__phones {
    position: relative;
    max-width: 440px;
    display: flex;
    flex-wrap: wrap;
    align-items: start;
    gap: 8px 16px;
    padding-left: 30px;
    margin-bottom: 10px;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 20px;
      height: 20px;
      background-image: url("../img/phone.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }
    .vacancies__phone {
      white-space: nowrap;
    }
  }
}
.vacancy {
  &__flex {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 50px;
  }
  &__text {
    width: calc(100% - 325px);
    display: grid;
    grid-template-columns: 100%;
    gap: 30px;
    background: #F4F4F4;
    border-radius: 10px;
    padding: 40px;
    h5 {
      font-size: 20px;
      line-height: 140%;
      font-weight: 700;
    }
    ul {
      display: grid;
      grid-template-columns: 100%;
      gap: 12px;
      margin-top: 12px;
      li {
        font-size: 16px;
        line-height: 140%;
        font-weight: 500;
      }
    }
  }
  &__group {
    width: 275px;
    flex-shrink: 0;
  }
  &__price-text {
    font-size: 24px;
    line-height: 100%;
    font-weight: 700;
  }
  &__price {
    font-size: 32px;
    line-height: 100%;
    font-weight: 600;
    color: #ED1C24;
    margin-top: 10px;
  }
  &__btns {
    margin-top: 60px;
    .vacancies__btn {
      margin-top: 40px;
    }
  }
}
.news-main {
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
    gap: 20px;
    .news-card {
      max-width: 100% !important;
      border-radius: 10px;
      &:hover {
        transform: unset;
        margin: 0 !important;
      }
      &__img {
        img {
          border-radius: 10px 10px 0 0;
        }
      }
      &__desc {
        padding: 20px;
      }
      &__title {
        font-size: 16px;
      }
    }
  }
}
.team {
  &__cards {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 40px / 3));
    gap: 60px 20px;
  }
  &-card {
    &__img {
      position: relative;
      padding-bottom: 143%;
      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: 20px;
      }
    }
    &__desc {
      margin-top: 20px;
    }
    &__name {
      font-size: 24px;
      line-height: 100%;
      font-weight: 700;
    }
    &__post {
      font-size: 16px;
      line-height: 100%;
      font-weight: 500;
      margin-top: 20px;
    }
    &__phone {
      display: flex;
      align-items: center;
      gap: 10px;
      margin-top: 20px;
      &:hover {
        color: #ED1C24;
      }
      i {
        width: 20px;
        flex-shrink: 0;
        img {
          width: 100%;
        }
      }
      span {
        font-size: 16px;
        line-height: 100%;
        font-weight: 500;
      }
    }
  }
}
.delivery {
  &__content {
    .vacancies__wrap {
      margin-top: 60px;
    }
    .contacts__items2 {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
      margin-top: 60px;
    }
  }
  &__subtitle {
    font-size: 24px;
    line-height: 140%;
    font-weight: 500;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 60px;
    margin-top: 60px;
    h3 {
      font-size: 32px;
      line-height: 100%;
      font-weight: 600;
      margin-bottom: 20px;
    }
    h5 {
      font-size: 20px;
      line-height: 140%;
      font-weight: 700;
      margin-bottom: 10px;
    }
    ul {
      display: grid;
      grid-template-columns: 100%;
      gap: 10px;
      li {
        position: relative;
        padding-left: 20px;
        font-size: 20px;
        line-height: 140%;
        font-weight: 500;
        &:before {
          content: '';
          position: absolute;
          top: 9px;
          left: 0;
          width: 7px;
          height: 7px;
          background: #ED1C24;
          border-radius: 50%;
        }
      }
    }
    p {
      font-size: 20px;
      line-height: 140%;
      font-weight: 500;
    }
  }
}


@media screen and (max-width: 1600px) {
  .general {
    padding: 24px 0 130px;
    &__inner {
      gap: 25px;
    }
    .sidebar {
      width: 265px;
      &__link {
        font-size: 18px;
      }
    }
    &__content {
      width: calc(100% - 290px);
    }
    &__title {
      margin-bottom: 40px;
    }
    .pagination {
      margin-top: 80px;
    }
  }
  .vacancies {
    &__wrap {
      gap: 20px;
      padding: 50px;
    }
    &__group {
      gap: 30px;
    }
    &__subtitle {
      font-size: 18px;
    }
    &__name {
      font-size: 18px;
    }
    &__post {
      font-size: 14px;
    }
    &__phone,
    &__email {
      span {
        font-size: 18px;
      }
    }
    &__download {
      span {
        font-size: 18px;
      }
    }
    &__btn {
      height: 50px;
      span {
        font-size: 18px;
      }
    }
    &__nav {
      margin-top: 60px;
    }
    &__list {
      margin-top: 40px;
      li a {
        font-size: 18px;
      }
    }
  }
  .vacancy {
    &__flex {
      gap: 40px;
    }
    &__text {
      width: calc(100% - 300px);
      h5 {
        font-size: 18px;
      }
    }
    &__group {
      width: 260px;
    }
    &__price-text {
      font-size: 20px;
    }
    &__price {
      font-size: 26px;
    }
    &__btns {
      margin-top: 40px;
      .vacancies__btn {
        margin-top: 30px;
      }
    }
  }
  .news-main {
    &__cards {
      .news-card {
        &__date {
          font-size: 16px;
        }
        &__title {
          font-size: 14px;
        }
      }
    }
  }
  .team {
    &-card {
      &__name {
        font-size: 20px;
      }
      &__post {
        font-size: 14px;
      }
    }
  }
  .delivery {
    &__content {
      .vacancies__wrap {
        margin-top: 40px;
      }
      .contacts__items2 {
        margin-top: 40px;
      }
    }
    &__subtitle {
      font-size: 20px;
    }
    &__text {
      gap: 40px;
      margin-top: 40px;
      h3 {
        font-size: 26px;
      }
      h5 {
        font-size: 18px;
      }
      ul {
        li {
          font-size: 16px;
          &:before {
            top: 7px;
          }
        }
      }
      p {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .vacancies {
    &__wrap {
      padding: 50px 40px;
    }
    &__phone,
    &__email {
      font-size: 16px;
    }
    &__btn {
      padding: 5px 30px;
    }
  }
  .team {
    &-card {
      &__name {
        font-size: 18px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .general {
    &__inner {
      flex-direction: column;
      gap: 60px;
    }
    .sidebar {
      width: 100%;
      order: 1;
      &__list {
        li {
          text-align: center;
          border-bottom: 1px solid #DDDDDD;
          padding-bottom: 12px;
        }
      }
    }
    &__content {
      width: 100%;
    }
    .pagination {
      margin-top: 60px;
    }
  }
}

@media screen and (max-width: 768px) {
  .vacancies {
    &__wrap {
      flex-direction: column;
      align-items: start;
      gap: 30px;
    }
    &__phone,
    &__email {
      span {
        font-size: 18px;
      }
    }
  }
  .vacancy {
    &__flex {
      flex-direction: column;
      gap: 60px;
    }
    &__text {
      width: 100%;
    }
    &__group {
      width: 100%;
    }
  }
  .news-main {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 20px / 2));
    }
  }
  .team {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 15px / 2));
      gap: 30px 15px;
    }
  }
  .delivery {
    &__content {
      .contacts__items2 {
        grid-template-columns: 100%;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .general {
    padding: 20px 0 80px;
    &__title {
      margin-bottom: 30px;
    }
    .pagination {
      margin-top: 40px;
    }
  }
  .vacancies {
    &__wrap {
      padding: 50px 20px;
    }
    &__group {
      width: 100%;
    }
    &__btn {
      width: 100%;
    }
    &__list {
      margin-top: 30px;
    }
  }
  .vacancy {
    &__text {
      padding: 30px 24px;
    }
  }
  .news-main {
    &__cards {
      grid-template-columns: 100%;
      gap: 30px;
    }
  }
  .team {
    &-card {
      &__desc {
        margin-top: 16px;
      }
      &__name {
        font-size: 14px;
      }
      &__post {
        font-size: 13px;
        margin-top: 12px;
      }
      &__phone {
        gap: 5px;
        margin-top: 12px;
        i {
          width: 18px;
        }
        span {
          font-size: 13px;
        }
      }
    }
  }
  .delivery {
    &__content {
      .general__title {
        margin-bottom: 30px;
      }
    }
    &__subtitle {
      font-size: 18px;
    }
    &__text {
      gap: 30px;
      h3 {
        font-size: 22px;
      }
    }
  }
}

@media screen and (max-width: 374px) {
  .team {
    &-card {
      &__name {
        font-size: 13px;
      }
      &__post {
        font-size: 12px;
      }
      &__phone {
        span {
          font-size: 12px;
        }
      }
    }
  }
}