.footer {
  &__group {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 20px;
  }
  &__logo {
    width: 250px;
    flex-shrink: 0;
    display: block;
    img {
      width: 100%;
    }
  }
  &__subgroup {
    max-width: 700px;
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
  }
  &__text {
    max-width: 390px;
    font-size: 16px;
    line-height: 100%;
  }
  &__btn {
    height: 60px;
    padding: 5px 40px;
    span {
      font-size: 20px;
    }
  }
  &__group2 {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 20px;
    margin-top: 60px;
  }
  &__subgroup2 {
    max-width: 350px;
    flex-shrink: 0;
  }
  &__subtitle {
    font-size: 24px;
    line-height: 100%;
    font-weight: 600;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin-top: 40px;
  }
  &__item {
    font-size: 20px;
    line-height: 100%;
  }
  &__phone {
    font-weight: 500;
  }
  &__email {
    text-decoration: underline;
    margin-top: 5px;
    &:hover {
      text-decoration: unset;
    }
  }
  &__phone,
  &__email {
    display: block;
    &:hover {
      color: #ED1C24;
    }
  }
  &__items2 {
    max-width: 700px;
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 20px;
  }
  &__item2 {
    min-width: 210px;
  }
  &__nav {
    display: flex;
    align-items: start;
    gap: 20px;
    margin-top: 40px;
  }
  &__list {
    min-width: 164px;
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    li a {
      font-size: 16px;
      line-height: 100%;
      &:hover {
        color: #ED1C24;
        text-decoration: underline;
      }
    }
  }
  &__group3 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    margin-top: 60px;
  }
  &__socials {
    display: flex;
    align-items: center;
    gap: 20px;
  }
  &__links {
    max-width: 900px;
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
  }
  &__link {
    font-size: 16px;
    line-height: 100%;
    &:hover {
      color: #ED1C24;
      text-decoration: underline;
    }
  }
  &__text2 {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    p {
      font-size: 16px;
      line-height: 100%;
    }
    p a:hover {
      color: #ED1C24;
      text-decoration: underline;
    }
  }
}
.footer-top {
  max-width: 1750px;
  width: calc(100% - 60px);
  background: #414042;
  border-radius: 50px;
  padding: 90px 30px;
  color: #fff;
  margin: 0 auto;
}
.footer-bottom {
  width: calc(100% - 60px);
  padding: 40px 30px;
  margin: 0 auto;
}

@media screen and (max-width: 1600px) {
  .footer {
    &__logo {
      width: 200px;
    }
    &__subgroup {
      max-width: 650px;
    }
    &__text {
      max-width: 360px;
      font-size: 14px;
    }
    &__btn {
      height: 50px;
      padding: 5px 30px;
      span {
        font-size: 18px;
      }
    }
    &__group2 {
      margin-top: 50px;
    }
    &__subtitle {
      font-size: 20px;
    }
    &__items {
      gap: 15px;
      margin-top: 30px;
    }
    &__item {
      font-size: 18px;
    }
    &__items2 {
      max-width: 650px;
    }
    &__nav {
      margin-top: 30px;
    }
    &__list {
      li a {
        font-size: 14px;
      }
    }
    &__group3 {
      margin-top: 50px;
    }
    &__links {
      max-width: 750px;
    }
    &__link {
      font-size: 14px;
    }
  }
  .footer-top {
    padding-top: 80px;
    padding-bottom: 80px;
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &__group {
      flex-direction: column;
      gap: 30px;
    }
    &__group2 {
      flex-direction: column;
      gap: 30px;
      margin-top: 40px;
    }
    &__items2 {
      order: -1;
    }
    &__group3 {
      flex-direction: column;
      align-items: start;
      gap: 30px;
      margin-top: 40px;
    }
    &__links {
      max-width: 100%;
      width: 100%;
      flex-wrap: wrap;
      justify-content: start;
      gap: 20px 30px;
    }
    &__text2 {
      p {
        font-size: 14px;
      }
    }
  }
  .footer-top {
    border-radius: 30px;
    padding: 40px 0;
  }
  .footer-bottom {
    padding-left: 0;
    padding-right: 0;
  }
}

@media screen and (max-width: 768px) {
  .footer {
    &__subgroup {
      flex-direction: column;
      align-items: start;
      gap: 30px;
    }
    &__items2 {
      max-width: 100%;
      flex-direction: column;
      gap: 30px;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer {
    &__logo {
      width: 140px;
    }
    &__text {
      font-size: 13px;
    }
    &__item {
      font-size: 16px;
    }
    &__item2 {
      min-width: unset;
      font-size: 16px;
    }
    &__list {
      min-width: 145px;
    }
    &__link {
      font-size: 13px;
    }
    &__text2 {
      p {
        font-size: 13px;
      }
    }
  }
  .footer-top {
    width: calc(100% - 30px);
  }
  .footer-bottom {
    width: calc(100% - 30px);
  }
}

@media screen and (max-width: 374px) {
  .footer {
    &__nav {
      flex-direction: column;
      gap: 15px;
    }
  }
}